
<template>
    <section id="main-content" class="main-content">        
        <loader :is-visible="isLoading"></loader>
        <section class="page-action">        
                <div class="row">
                    <div class="col-md-12 padding-t_8">
                        <div class="theme-primary partition-full">
                            <span class="p-name text-white">{{$t('BulkUpload')}}</span>
                            <span class="p-actions float-right">
                                <a href="javascript:;" class="p-action-btn text-white" id="PageTour" title="Page Tour" @click="setTourGuideSteps(0)"><i class="fa fa-road" aria-hidden="true"></i><br>Page Tour</a>
                                </span>
                        </div>
                    </div>
                </div>       
        </section>
        <div class="user-guide-custom d-none">
            {{$t('User_Guide_ExpenseImport')}}
        </div>   
        <div class="border p-3" v-if="!filepath">                  
            <div class="row" >
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('DownloadSampleFile')}}:</label>
                        <div class="input-group mb-3">
                            <div class="form-control">
                                <i class="fa fa-file-excel-o pr-1 text-success"></i>{{$t('Expense')}}.xls
                            </div>
                            <div class="input-group-append downloadExpense">
                                <span class="input-group-text" id="basic-addon2">
                                    <a href="/Content/files/Expense.xls" class="" download>
                                        <i class="fa fa-download text-primary"></i>
                                    </a>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">                       
                        <label>{{$t('UploadaFile')}}:<span class="text-danger"></span></label>
                        <div class="input-group">
                            <div class="custom-file expenseFile">
                                <input  type="file" name='FileSrc' id='flAvatar' multiple="true"  tabindex="-1" @change="setFile($event)" style="position: absolute; clip: rect(0px, 0px, 0px, 0px);">
                                <div class="bootstrap-filestyle input-group">
                                    <input type="text" class="form-control disabled" disabled="" v-model="attachmentName" /> 
                                    <div class="input-group-append" style="cursor:pointer;">
                                        <span class="group-span-filestyle input-group-btn input-group-text bg-white" tabindex="0">
                                            <label title="Click here to select a file." for='flAvatar' class="mb-0 font-weight-normal ">{{$t('FileControlText')}}</label>
                                        </span>
                                        <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                             <a href="javascript:;" class="round-icon-small btn-danger-light" title="Clear" @click="Clear"><i class="fa fa-undo"></i></a>                                            
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row flex-row-reverse">
                <div class="col-lg-6 text-right">
                   <a href="javascript:void(0);" @click="UploadExpenseFile()" :title="$t('ImportFromExcelFile')" class="btn btn-info formbtn mr-1"><i class="fa fa-file-excel-o mr-2"></i>{{$t('ImportFromExcelFile')}}</a>
                   <RouterLink to="/Expenses/Index" title="Cancel" class="btn btn-danger formbtn widthhalf" tabindex="4"><i
                            class="fa fa-close pr-2"></i>{{$t('Cancel')}}</RouterLink>                           
                </div>
            </div>
           
        </div>
        <ImportExpenseDeatils v-if="filepath && filepath.length>0" :path="filepath"></ImportExpenseDeatils>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
import ImportExpenseDeatils from '../Common/ImportExpenseDetail.vue'
export default {    
    components: {
        ImportExpenseDeatils        
      },
    data: function () {
        return {
            isLoading: false,
            file: null,
            filepath: null,
            excelFileName: null,            
            files:new FormData() ,
            value:null,
            attachmentName:'' 
        }
    },
    methods:{
        setFile: function (e) {
            
            this.attachmentName=e.target.files[0].name;
            this.file = e.target.files[0];
            this.excelFileName = e.target.files[0].name;
        },
        Clear:function(){
            this.attachmentName='';
        },
        UploadExpenseFile() {
            var vm = this;
            if ($("#flAvatar").val() != "") {
                var file = $("#flAvatar").val().split('\\').pop();
                var allowFileTypesExt = '.xls,.xlsx';
                var extension = file.replace(/^.*\./, '');
                if (!allowFileTypesExt.includes(extension)) {
                    vm.ShowAlert(vm.$t('InvalidSampleFileUploadMsg', [file, allowFileTypesExt]), "warning", true, vm.$t('Alert'));
                }
                else {
                    const formdata = new FormData();
                    formdata.append("file", vm.file);
                    vm.isLoading=true;
                    DataService.ImportExpensePost(formdata).then(function (response) {
                        vm.isLoading=false;
                        var data = response.data;
                        if (data.Status == 0) {
                            vm.filepath=data.Msg;
                        }
                        else {
                            vm.ShowAlert(data.Msg, "warning", true, vm.$t("Alert"));
                        }
                    });
                }
            }else{
                vm.ShowAlert(vm.$t("PleaseUploadExcelFile"), "warning", true, vm.$t("Alert"));
            }
        },
    }
}
</script>