<template>
    <section id="divMainListData" class="main-content">
        <loader :is-visible="isLoading"></loader>
        <section class="main-content">
            <div class="container-fluid padding-t_8">                
                <div class="col-12 mb-2 p-0">
                    <div class="mr-2 d-inline-block mt-2">
                        <span class="legend-valid colorboxsquare"></span> {{$t('Valid')}}
                    </div>
                    <div class="mr-2 d-inline-block mt-2">
                        <span class="legend-expenselimit colorboxsquare"></span>{{$t('PerDayExpenseExceeded')}}
                    </div>
                    <div class="mr-2 d-inline-block mt-2">
                        <span class="legend-totalexpenselimit colorboxsquare"></span> {{$t('ExpenseLimitExceeded')}}
                    </div>
                </div>
                <div class="col-md-12 p-0" id="divShowlistingData">
                    <div class="listing">
                        <Form ref="form" v-slot="{  valid, errors}" >
                        <div class="table-responsive" style="min-height: 415px;">
                            <table id="tblExpList"
                                class="sortable bulkexpense table table-bordered dt-responsive nowrap mt-0">
                                <thead class="thead-bg">
                                    <tr>
                                        <th :style="{'width':($t('ExpenseDate').length * 12 + 100)+'px' }">
                                            <span
                                                :style="{'min-width':($t('ExpenseDate').length * 12 + 100)+'px'}">{{$t('ExpenseDate')}}</span>
                                        </th>
                                        <th :style="{'width':($t('Trip').length * 12 + 100)+'px' }">
                                            <span
                                                :style="{'width':($t('Trip').length * 12 + 100)+'px' }">{{$t('Trip')}}</span>
                                        </th>
                                        <th :style="{'width':($t('ExpenseTitle').length * 12 + 100)+'px' }">
                                            <span
                                                :style="{'width':($t('ExpenseTitle').length * 12 + 100)+'px' }">{{$t('expense_title')}}</span>
                                        </th>
                                        <th :style="{'width':($t('Category').length * 12 + 100)+'px' }">
                                            <span
                                                :style="{'width':($t('Category').length * 12 + 100)+'px' }">{{$t('Category')}}</span>
                                        </th>
                                        <th :style="{'width':($t('Project').length * 12 + 100)+'px' }">
                                            <span
                                                :style="{'width':($t('Project').length * 12 + 100)+'px' }">{{$t('Project')}}</span>
                                        </th>
                                        <th :style="{'width':($t('Currency').length * 12 + 100)+'px' }">
                                            <span
                                                :style="{'width':($t('Currency').length * 12 + 100)+'px' }">{{$t('Currency')}}</span>
                                        </th>
                                        <th :style="{'width':($t('Task').length * 12 + 100)+'px' }">
                                            <span
                                                :style="{'width':($t('Task').length * 12 + 100)+'px' }">{{$t('Task')}}</span>
                                        </th>
                                        <th :style="{'width':($t('Amount').length * 12 + 100)+'px' }">
                                            <span
                                                :style="{'width':($t('Amount').length * 12 + 100)+'px' }">{{$t('Amount')}}</span>
                                        </th>
                                        <th :style="{'width':($t('Description').length * 12 + 100)+'px' }">
                                            <span
                                                :style="{'width':($t('Description').length * 12 + 100)+'px' }">{{$t('Description')}}</span>
                                        </th>
                                        <th :style="{'width':($t('Attachment').length * 12 + 100)+'px' }">
                                            <span
                                                :style="{'width':($t('Attachment').length * 12 + 100)+'px' }">{{$t('Attachment')}}</span>
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-if="expListing == null">
                                        <td colspan="8" class="text-center text-danger no-record">
                                       {{ $t('NoRecordfound') }}
                                        </td>
                                    </tr>
                                    <tr class="seletedrow" v-for="(item,index) in expListing" :key="index">                                                                    
                                        <td style="overflow:visible!important;">                                         
                                            <div class="input-group">        
                                            <Field :rules="{ 'required': true }"  :name="'datepicker'+index" v-slot="{ errors,changed}">                                              
                                                    <v-date-picker :name="'datepicker'+index" :minute-increment="5" class="input-group" v-model="item.expenseDate"  @update:modelValue="changed">
                                                        <template v-slot="{ inputValue, inputEvents, togglePopover}">
                                                            <input  v-bind:class="{ 'form-control': true, 'is-invalid': errors[0]}"
                                                                 :value="inputValue" v-on="inputEvents" placeholder="HH:MM:YYYY" />
                                                            <div @click="togglePopover()" class="input-group-append">
                                                            <span class="input-group-text"> <em class="fa fa-calendar"></em></span>
                                                           </div>
                                                        </template>
                                                    </v-date-picker>                                                   
                                                    <!-- <span :name="'datepicker'+index" v-if="errors[0]" class="invalid-feedback">{{errors[0]}}</span> -->
                                                </Field>
                                            </div>
                                        </td>
                                        <td>
                                            <Field  :rules="{ 'required': true }" :name="'tripId'+index"  v-slot="{ errors }">
                                                <select v-model="item.tripId" :name="'tripId'+index"
                                                    v-bind:class="{'form-control' : true,'is-invalid':errors[0]}" >
                                                    <option value="">Select Trip</option>
                                                    <option v-for="(option,optionIndex) in trips" :key="optionIndex" :value="String(option.triP_ID)">{{ option.triP_NAME }}</option>
                                                </select>
                                                <span v-if="errors[0]" :name="'tripId'+index" class="invalid-feedback">{{$t('TripValidate')}}</span>
                                            </Field>                                            
                                        </td>
                                        <td>
                                            <Field :rules="{'required': true}" :name="'expenseTitle'+index" v-slot="{errors}">
                                                <input v-model="item.expenseTitle" :name="'expenseTitle'+index" type="text"
                                                    v-bind:class="{'form-control' : true,'is-invalid': errors[0]}"  />
                                                    <span v-if="errors[0]"  :name="'expenseTitle'+index" class="invalid-feedback">{{$t('ExpenseTitleValidate')}}</span>                                          
                                            </Field> 
                                        </td>
                                        <td class="cat">
                                            <Field :rules="{ 'required': true }"  :name="'categoryId'+index" v-slot="{ errors}">
                                                <select v-model="item.categoryId"  :name="'categoryId'+index" 
                                                    v-bind:class="{'form-control' : true,'is-invalid':errors[0]}">
                                                    <option value="">Please Select Category</option>
                                                    <option v-for="(option,index1) in categories" :key="index1" :value="String(option.expensE_CATEGORY_ID)">{{ option.categorY_NAME }}</option>
                                                </select>
                                                <span v-if="errors[0]" :name="'categoryId'+index"   class="invalid-feedback">{{$t('CategoryValidate')}}</span>                                                                                     
                                            </Field> 
                                        </td>
                                        <td class="pro">
                                            <Field :rules="{ 'required': true }"    :name="'projectId'+index" v-slot="{ errors }">
                                                <select v-model="item.projectId"    :name="'projectId'+index" @change="GetTaskByProject($event, item)"
                                                    v-bind:class="{'form-control' : true,'is-invalid':errors[0]}">
                                                    <option value="">Please Select Project</option>
                                                    <option v-for="(option,index2) in projects" :key="index2" :value="option.projectId">{{ option.projectName }}</option>
                                                </select> 
                                                <span v-if="errors[0]"   :name="'projectId'+index" class="invalid-feedback">{{$t('ProjectValidate')}}</span>                                              
                                            </Field> 
                                                                                       
                                        </td>
                                        <td class="cur">
                                            <Field :rules="{ 'required': true }"  :name="'currencyId'+index"  v-slot="{ errors}">
                                                <select  v-model="item.currencyId"   :name="'currencyId'+index" 
                                                    v-bind:class="{'form-control' : true,'is-invalid':errors[0]}">
                                                    <option value="">Select Currency</option>
                                                    <option v-for="(option,index3) in currencies" :key="index3" :value="String(option.currencyId)">{{ option.currency }}</option>
                                                </select>                                                                                                                                 
                                            </Field> 
                                        </td>
                                        <td>
                                            <Field  name="Task" >
                                                <select   v-model="item.taskId"   :name="'taskId'+index" 
                                                    v-bind:class="{'form-control' : true}">
                                                    <option value="">Select Task</option>
                                                    <option v-for="(option,index4) in item.tasks" :key="index4" :value="String(option.task_id)">{{ option.task_name }}</option>
                                                </select>                                                                                                                                   
                                            </Field> 
                                        </td>
                                        <td>
                                            <Field :rules="{'required': true}" :name="'expenseAmount'+index" v-slot="{errors}">
                                                <input  :name="'expenseAmount'+index" type="text"  v-model="item.expenseAmount"
                                                    v-bind:class="{'form-control amt' : true,'is-invalid': errors[0]}"/>
                                                    <span v-if="errors[0]" :name="'expenseAmount'+index" class="invalid-feedback">{{$t('ExpenseAmountValidate')}}</span>                                          
                                            </Field> 
                                        </td>
                                        <td>
                                            <Field :rules="{'required': true}" :name="'description'+index" v-slot="{errors}">
                                                <textarea   v-model="item.description"  :name="'description'+index" cols="5"
                                                    v-bind:class="{'form-control' : true,'is-invalid': errors[0]}" maxlength="225"/>
                                                    <span v-if="errors[0]" class="invalid-feedback">{{errors[0]}}</span>                                                                                      
                                            </Field>
                                        </td>
                                        <td>
                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                            <div class="col-lg-12 float-left">
                                                <div class="form-group mb-1">
                                                    <div class="input-group">
                                                        <div class="custom-file">
                                                            <input  type="file" :name="'FileSrc'+index" :id="'flFile'+index" multiple="true"  tabindex="-1" v-on:change="setFile(index,$event)" style="position: absolute; clip: rect(0px, 0px, 0px, 0px);">
                                                            <div class="bootstrap-filestyle input-group">
                                                                <input type="text" class="form-control disabled" disabled="" v-model="item.attachmentName" /> 
                                                                <div class="input-group-append" style="cursor:pointer;">
                                                                    <span class="group-span-filestyle input-group-btn input-group-text bg-white" tabindex="0">
                                                                        <label title="Click here to select a file." :for="'flFile'+index" class="mb-0 font-weight-normal ">{{$t('FileControlText')}}</label>
                                                                    </span>
                                                                    <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                                                    <!-- <a href="javascript:;" class="round-icon-small btn-danger-light" title="Clear" @click="Clear(item)"><i class="fa fa-undo"></i></a> -->                                                                        
                                                                    </span>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>                                  
                                        </div>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <div class="text-right mt-3">
                            <a v-if="expListing != null" id="aValidate" href="javascript:void(0);" class="btn btn-warning mr-1"
                                :title="$t('ValidateExpenseList')" @click="ValidateExpense(valid,errors)"><i
                                    class="fa fa-list pr-2"></i>{{$t('ValidateExpenseList')}}</a>

                            <a v-if="expListing != null" id="aEdit" href="javascript:;" class="btn btn-success hidden mr-1"
                            :title="$t('Edit')" @click="ExpenseEdit"><i class="fa fa-save pr-2"></i>{{$t('Edit')}}</a>

                            <a v-if="expListing != null" id="aSubmit" href="javascript:void(0);" class="btn btn-success hidden mr-1"
                            @click="OnSubmitExpense"
                            :title="$t('ConfirmSubmit')"><i class="fa fa-save pr-2"></i>{{$t('ConfirmSubmit')}}</a>

                            <a @click="onCancel" href="javascript:void(0)" class="btn btn-danger mr-1" :title="$t('Cancel')"><i
                                    class="fa fa-close pr-2"></i>{{$t('Cancel')}}</a>
                        </div>
                    </Form>
                    </div>
                </div>
            </div>
        </section>
    </section>
   
</template>
<script>
import DataService from '../../services/DataService';
export default {
    props:{
        path:null
    },
    data: function() {
        return {
            isLoading: false,                   
            expLimitJsonBack:[],
            check_EXPENSE_LIMIT:false,
            userDateFormat:null,
            value:null,
            expListing:[],
            trips:[],
            categories:[],
            projects:[],
            currencies:[]
        }
    },
    created:async function(){ 
        this.userDateFormat=this.GetUserInfo.DateFormat;
        await this.UploadExpenseDetails()
    },
    methods:{ 
        async UploadExpenseDetails() { 
            var vm = this;
            var param = 'fullPath=' + vm.path;
            vm.isLoading=true;
           await DataService.UploadExpenseDetails(param).then(function (response) {   
                vm.isLoading=false;             
                if (response.data.msgFor == "success") {
                    var data = response.data;                 
                    data.listing.forEach(function(x){                        
                        x.tasks = [];
                        if(typeof(x.projectId) == 'undefined' || x.projectId == null || x.projectId == 0){
                            x.projectId = "";
                        }
                        if(typeof(x.tripId) == 'undefined' || x.tripId == null || x.tripId == 0){
                            x.tripId = "";
                        }
                        if(typeof(x.taskId) == 'undefined' || x.taskId == null || x.taskId == 0){
                            x.taskId = "";
                        }
                        if(typeof(x.categoryId) == 'undefined' || x.categoryId == null || x.categoryId == 0){
                            x.categoryId = "";
                        }
                    });
                    vm.expListing = data.listing;
                    vm.expListing.forEach((row)=>{
                        row.attachmentName=''
                         row.attachmentFiles=[];
                            row.tasks=[];
                            });
                    vm.trips = data.Trips;
                    vm.projects = data.Projects;
                    vm.trips = data.Trips;
                    vm.categories = data.Categories;
                    vm.projects = data.Projects;
                    vm.currencies = data.Currencies;
                }
            });

        },
        updateFileInputUI(){
            this.$set(this.expListing, 
            this.expListing.length, 
          JSON.parse(JSON.stringify(this.expListing))
         );
        },
        ExpenseEdit:function(){
          $("#aValidate").removeClass("hidden");
          $("#aEdit,#aSubmit").addClass("hidden");
        }, 
        onCancel: function () {            
            this.$router.push({ name: 'Expense' });
        }, 
        setFile:function(fileindex,e){ 
            
           var vm=this;
            for (let index = 0; index < e.target.files.length; index++) {
                const file = e.target.files[index];
                vm.expListing[fileindex].attachmentFiles.push(file);
                vm.expListing[fileindex].attachmentName= file.name+' ';                
            }               
        },       
        Clear:function(item){            
            item.attachmentName='';
            item.attachmentFiles=[];
            item.attachmentList=[];
        }, 
     
        GetTaskByProject(event, item) {         
            var projectId = $(event.target).val();
            var param = 'projectId=' + projectId;
            DataService.GetTaskByProject(param).then(function (response) {
                item.tasks = response.data;
            });
        },
        ConvertToDate(cDate) {
            var uDateFormate = this.userDateFormat;
            var browserDatePattern = 'M/d/yyyy';
            try {
                browserDatePattern = Sys.CultureInfo.CurrentCulture.dateTimeFormat.ShortDatePattern;
            } catch (e) { }
            var datePart = cDate.split('/');
            switch (uDateFormate) {
                case "MM/dd/yyyy":
                    switch (browserDatePattern) {
                        case "M/d/yyyy":
                            return new Date(datePart[0] + "/" + datePart[1] + "/" + datePart[2]);
                        default:
                            return new Date(datePart[1] + "/" + datePart[0] + "/" + datePart[2]);
                    }
                case "dd/MM/yyyy":
                    switch (browserDatePattern) {
                        case "M/d/yyyy":
                            return new Date(datePart[1] + "/" + datePart[0] + "/" + datePart[2]);
                        default:
                            return new Date(datePart[0] + "/" + datePart[1] + "/" + datePart[2]);
                    }
                default:
                    break;
            }
            return new Date(datePart[0] + "/" + datePart[1] + "/" + datePart[2]);
        },
        ValidateExpense(valid,errors) {
          var vm=this;                  
          vm.$refs.form.validate().then((result) => {             
            if(result){
              $("#aValidate").addClass("hidden");
              $("#aEdit,#aSubmit").removeClass("hidden");
              vm.check_EXPENSE_LIMIT = false;     
              vm.GetExpLimitJson();                   
              setTimeout(() => {
                vm.CheckFile();
              }, 1000);  
              }
            })
        },
        GetExpLimitJson(){ 
            var vm=this;
            DataService.GetExpenseLimitAvailableAmount().then(function (response) { 
               vm.expLimitJsonBack=response.data;
               vm.RowAmtValidation();
            });
        },
        RowAmtValidation() {
            var flag = true;
            var vm=this;
            $("#tblExpList tr.seletedrow").each(function (i, cRow) {
            var cCat = $(cRow).find(".cat select").val();
            var cPro = $(cRow).find(".pro select").val();
            var cProText = $(cRow).find(".pro select :selected").text();
            var cCur = $(cRow).find(".cur select").val();
            var ctxtDate = $(cRow).find("input.date").val();
            var cAmount = $(cRow).find("input.amt").val();
            var cDate = new Date();
            if (cCat == undefined) {
                cCat = $(cRow).find(".cat input[type=hidden]").val();
            }
            if (cPro == undefined) {
                cPro = $(cRow).find(".pro input[type=hidden]").val();
            }
            if (cCur == undefined) {
                cCur = $(cRow).find(".cur input[type=hidden]").val();
            }
            if (cCat == "" || cPro == "" || cCur == "") {
                return false;
            }
            if (ctxtDate != undefined) {
                if (ctxtDate != "") {
                    cDate = vm.ConvertToDate(ctxtDate);
                }
                else {
                    return false;
                }
            }
            $.each(vm.expLimitJsonBack, function (i, exp) {
                if (exp.CURRENCY_ID == cCur && exp.PROJECT_ID == cPro && exp.EXPENSE_CATEGORY_ID == cCat && (new Date(parseInt(exp.EXPENSE_LIMIT_FROM.replace('/Date(', ''))) <= cDate && cDate < new Date(parseInt(exp.EXPENSE_LIMIT_TO.replace('/Date(', ''))))) {
                    if (exp.EXPENSE_PER_DAY != null && parseFloat(cAmount) > parseFloat(exp.EXPENSE_PER_DAY)) {
                        $(cRow).removeClass("medium-bar");
                        $(cRow).removeClass("high-profile-bar");
                        $(cRow).addClass("high-bar");
                        vm.ShowAlert(vm.$t('PerDayExpenseLimit',[exp.EXPENSE_PER_DAY, cProText]),"warning", true, vm.$t("Alert"));
                        vm.check_EXPENSE_LIMIT = true;
                        flag = false;
                    }
                    else if (exp.EXPENSE_OVER_LIMIT == 0) {
                        if ((parseFloat(exp.MAX_AMT) - parseFloat(cAmount)) < 0) {                       
                            $(cRow).removeClass("medium-bar");
                            $(cRow).removeClass("high-bar");
                            $(cRow).addClass("high-profile-bar");
                            vm.ShowAlert(vm.$t('TotalExpenseLimit',[exp.EXPENSE_MAX, cProText]),  "warning", true, vm.$t("Alert"));
                            vm.check_EXPENSE_LIMIT = true;
                            flag = false;
                        }
                        else {
                            exp.MAX_AMT = parseFloat(exp.MAX_AMT) - parseFloat(cAmount);
                            $(cRow).removeClass("high-profile-bar");
                            $(cRow).removeClass("high-bar");
                            $(cRow).addClass("medium-bar");
                            flag = true;
                        }
                    }
                    else {
                        $(cRow).removeClass("high-profile-bar");
                        $(cRow).removeClass("high-bar");
                        $(cRow).addClass("medium-bar");
                    }
                }
            });
            if (flag == true) {
                $(cRow).removeClass("high-profile-bar");
                $(cRow).removeClass("high-bar");
                $(cRow).addClass("medium-bar");
            }
            });
            return vm.check_EXPENSE_LIMIT;
        },
        onChangeFile(files){
          this.files.append('file',files[0],files[0].name);
        },
        CheckFile(){
            var vm= this;
            vm.isLoading = true;  
          const formdata = this.files;
                  DataService.CheckFileSizeAndType(formdata).then(function (response) {
                            vm.isLoading = false;  
                    });
        },
        OnSubmitExpense(){
            var vm=this;
          vm.$refs.form.validate().then((result) => {
           
            if(result){
                if(!vm.check_EXPENSE_LIMIT){
                            var postData=[];
                            const formData = new FormData();
                                vm.expListing.forEach((item,index) => {
                                    
                                var obj = {} 
                                var fileRefNo='fileRefNo'+index;
                                obj['fileRefNo']=fileRefNo;
                                obj['ExpenseTitle']=item.expenseTitle;
                                obj['TripId']=item.tripId?String(item.tripId):'0';
                                obj['ExpenseDate']=item.expenseDate;
                                obj['CategoryId']=item.categoryId?String(item.categoryId):'0';
                                obj['ProjectId']=item.projectId?String(item.projectId):'0';
                                obj['CurrencyId']=item.currencyId?String(item.currencyId):'0';
                                obj['TaskId']=item.taskId?String(item.taskId):'0';
                                obj['ExpenseAmount']=item.expenseAmount?String(item.expenseAmount):'0';
                                obj['Description']=item.description;
                                obj['IsBulkUpload']=1;
                                postData.push(obj);
                                
                                for (var i = 0; i < item.attachmentFiles.length; i++) {
                                let file = item.attachmentFiles[i];
                                formData.append(fileRefNo + '_files[' + i + ']', file);
                                }
                                })  
                   
                    formData.append('postString',JSON.stringify(postData));
                    DataService.SaveExpenseBulk(formData).then(function (response) {
                            vm.isLoading = false;  
                        if (response.status==200) {
                            vm.ShowAlert(vm.$t('ExpenseAdd', [vm.$t('Expense')]), "success", true, vm.$t("Alert"));
                            vm.$router.push({ name: 'Expense' });
                        }else{                           
                            vm.ShowAlert(vm.$t('AddedError', [vm.$t('Expense')]), "warning", true, vm.$t("Alert"));
                        }
                    });
                }         
            }
          })
          }
        }
    }

</script>